import {AuthAPI, HeaderAPI} from "../../API/API";

const SET_AUTH_USER_DATA = "SET-AUTH-USER-DATA";
const SET_USER_DATA = "SET-USER-DATA";
const SET_LOGIN = "SET-LOGIN";
const SET_LOG_OUT = "SET-LOG-OUT"
const GET_CAPTCHA = "GET-CAPTCHA"
let initialState = {
    id: null,
    email: null,
    login: null,
    isLogin: false,
    date: null,
    captchaURL: null,
}


export const AuthReducer = (state = initialState, action) => {

    switch (action.type) {
        case SET_AUTH_USER_DATA: {

            return {
                ...state,
                ...action.data,
            }
        }
        case SET_USER_DATA: {

            return {
                ...state,
                date: action.data,
            }
        }
        case GET_CAPTCHA: {

            return {
                ...state,
                captchaURL: action.captcha.data.url
            }
        }
        case SET_LOG_OUT: {
            return {
                ...state,
                id: null,
                email: null,
                login: null,
                isLogin: false,
                date: null
            }
        }
        default:
            return state
    }

}

export let SetAuthUserData = (id, email, login, isLogin) => ({

    type: SET_AUTH_USER_DATA,
    data: {id, email, login, isLogin}
})
export let SetLogOut = () => ({

    type: SET_LOG_OUT,

})
export let SetUserData = (data) => ({

    type: SET_USER_DATA,
    data: data
})
export let GetCaptcha = (captchaURL) => ({
    type: GET_CAPTCHA,
    captcha: captchaURL
})
//thunk
export const HeaderLoginThunk = () => (dispatch) => {

    return HeaderAPI.AuthMe().then((a) => {

        if (a.data.resultCode === 0) {
            let {id, email, login} = a.data.data;
            dispatch((SetAuthUserData(id, email, login, true)))

            HeaderAPI.Login(login).then((b) => {

                b.data.items.filter((u) => {
                    if (id === u.id) {
                        dispatch(SetUserData(u))

                        }
                    })

                })
            }
        else {
            let {id, email, login} = a.data.data;
            HeaderAPI.Login(login).then((b) => {

                b.data.items.filter((u) => {
                    if (id === u.id) {
                        dispatch(SetUserData(u))

                    }
                })

            })
        }
        })

}
export const LoginThunk = (data, setStatus) => {
    return (dispatch) => {

        AuthAPI.Login(data).then((response) => {

            if (response.data.data.resultCode === 0) {
                dispatch(HeaderLoginThunk())
            } else {
                if (response.data.resultCode === 10) {
                    dispatch(GetCaptchaURL())
                }
                setStatus({error: response.data.messages})
            }
        })
    }
}
export const UnLoginThunk = () => {
    return (dispatch) => {

        AuthAPI.LogOut().then((response) => {

            if (response.data.resultCode === 0) {
                dispatch(SetLogOut())
            }
        })
    }
}
export const GetCaptchaURL = () => {
    return (dispatch) => {
        AuthAPI.GetCaptcha().then((captchaURL) => {
            dispatch(GetCaptcha(captchaURL))
        })
    }
}
